<script>
import Loading from '@shell/components/Loading';
import CruResource from '@shell/components/CruResource';
import NameNsDescription from '@shell/components/form/NameNsDescription';
import { LabeledInput } from '@components/Form/LabeledInput';
import Tabbed from '@shell/components/Tabbed';
import Tab from '@shell/components/Tabbed/Tab';
import CreateEditView from '@shell/mixins/create-edit-view';
import FormValidation from '@shell/mixins/form-validation';
import { _CREATE, _VIEW } from '@shell/config/query-params';
import { exceptionToErrorsArray } from '@shell/utils/error';
import { LONGHORN_NAMESPACE } from '@longhorn/types/longhorn';

export default {
  name: 'EditEngineImage',

  components: {
    Loading,
    CruResource,
    NameNsDescription,
    LabeledInput,
    Tabbed,
    Tab,
  },

  mixins: [CreateEditView, FormValidation],

  props: {
    mode: {
      type: String,
      default: _CREATE,
    },
    value: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      fvFormRuleSets: [
        {
          path: 'spec.image',
          rules: ['required'],
          translationKey: 'longhorn.engineImage.form.image',
        },
      ],
      LONGHORN_NAMESPACE,
      _VIEW,
    };
  },

  created() {
    if (!this.value.spec) {
      this.value.spec = {};
    }
    if (this.value.spec.image === undefined) {
      this.value.spec.image = '';
    }
  },

  methods: {
    onError(errors) {
      this.errors = Array.isArray(errors) ? errors : [errors];
    },

    async save(buttonDone) {
      this.errors = [];

      if (!this.value.spec.image) {
        this.errors.push(this.t('longhorn.engineImage.form.validation.imageRequired'));
        buttonDone(false);

        return;
      }

      try {
        await this.actuallySave();
        buttonDone(true);
        this.done();
      } catch (err) {
        this.errors = exceptionToErrorsArray(err);
      }
    },
  },
};
</script>

<template>
  <Loading v-if="$fetchState.pending" />
  <div v-else>
    <CruResource
      :done-route="doneRoute"
      :mode="mode"
      :resource="value"
      :validation-passed="fvFormIsValid"
      :errors="fvUnreportedValidationErrors"
      @finish="save"
      @cancel="done"
      @error="onError"
    >
      <NameNsDescription
        :value="value"
        :mode="mode"
        :name-hidden="mode !== _VIEW"
        :description-hidden="true"
        :force-namespace="LONGHORN_NAMESPACE"
      />
      <Tabbed side-tabs :resource="value">
        <Tab name="basics" label-key="longhorn.engineImage.tab.basics">
          <LabeledInput
            v-model:value="value.spec.image"
            label-key="longhorn.engineImage.form.image"
            :mode="mode"
            :rules="fvGetAndReportPathRules('spec.image')"
          />
        </Tab>
      </Tabbed>
    </CruResource>
  </div>
</template>
